'use strict';

/**
* Return a string created by joining strided array elements using a specified separator.
*
* @module @stdlib/blas-ext-base-gjoin
*
* @example
* import gjoin from '@stdlib/blas-ext-base-gjoin';
*
* var x = [ 1, 2, 3, 4 ];
*
* var str = gjoin( x.length, ',', x, 1 );
* // returns '1,2,3,4'
*
* @example
* import gjoin from '@stdlib/blas-ext-base-gjoin';
*
* var x = [ 1, 2, 3, 4 ];
*
* var str = gjoin.ndarray( x.length, ',', x, 1, 0 );
* // returns '1,2,3,4'
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import ndarray from './ndarray.js';


// MAIN //

setReadOnly( main, 'ndarray', ndarray );


// EXPORTS //

export default main;
